html {
    background-color: #92C591;
    font-family: 'Roboto', sans-serif;
}

h1 {
    font-weight: 900;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    margin-bottom: 0;
    margin-top: 5em;
    color: #305A30;
}

h3 {
    font-weight: 900;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    margin-bottom: 2em;
    margin-top: 5em;
    color: #305A30;
}

.score {
    display: flex;
    justify-content: center;
    margin-bottom: 3em;
}

button {
    border: 3px solid #305A30;
    border-radius: 24px;
    border-radius: 24px;
    background-color: #92C591;
    font-weight: 900;
    text-transform: uppercase;
    font-size: 16px;
    color: #0A3409;
    letter-spacing: 2px;
    padding-left: 23px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
}

#aloitusruutu {
    display: flex;
    justify-content: center;
}

input {
    background-color: #92C591;
    border: none;
    border-bottom: 1px solid #305A30;
    padding: 1em;
    padding-bottom: 0;
}

#pelaaja {
   margin-top: 2em;
   margin-bottom: 1em; 
}

.flex {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#second {
    margin-bottom: 2em;
}

.players {
    font-size: 1.2em;
}

#winner {
    font-size: 2em;
    display: flex;
    justify-content: center;
}

.singlePlayer {
    float: right;
    padding-top: 1em;
    padding-right: 1em;
}

.singlePlayer button {
    margin-left: 1.3em;
    padding-left: 2em;
    padding-right: 2em;
    margin-bottom: 0.6em;
    font-weight: 300;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
}